@import "colors";

.profile {
  background-color: skyblue;
  width: 100%;
  height: 100vh;

  > main {
    max-width: 900px;
    height: 100%;
    background-color: white;
    margin: auto;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 4rem;

    > img {
      background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 2px rgb(255 255 255 / 30%);
    
    justify-self: center;
    width: 300px;
    height: 300px;
    animation: profile__animate 8s ease-in-out infinite 1s;
      @keyframes profile__animate{
        0%{
            border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
        }
        50%{
            border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
        }
        100%{
            border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
        }
    }
    
    }

    > h5 {
      font-size: 2rem;
    }
    > div {
      margin: 2rem;
      > a {
        background-color: $red;
        color: white;
        padding: 0.7rem 1.5rem;
        font-size: 1rem;
        border-radius: 5px;
        display: flex;
        align-items: center;
        svg {
          margin-right: 0.5rem;
        }
        &:hover {
          background-color: $reddark;
        }
      }
    }

    > button {
      border: none;
      outline: none;
      padding: 0.7rem 1.5rem;
      border-radius: 5px;
      cursor: pointer;
      font-weight: 500;
      font-size: 1rem;
      &:hover {
        background-color: rgba(245, 245, 245, 0.864);
      }
    }
  }
}
