@import "colors";

footer {
  min-height: 300px;
  background-color: darkgray;
  padding: 4rem;
  display: grid;
  grid-template-columns: 2fr 1fr;
  color: black;
  align-items: center;

  h2 {
    color: black;
    font-family: fantasy;
    font-weight: 400;
    font-size: 2rem;
    margin-left: 1rem;
  }

  p,
  strong {
    margin: 1rem;
  }

  aside {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
    margin-bottom: 1rem;
    h4 {
      font-size: 1.1rem;
    }

    a {
      margin: 0.5rem;
      font-size: 2rem;
      color: #413F42;
      display: flex;
      flex-direction: row;
      &:hover {
        color: black;
      }
    }
    
  }
}

@media screen and (max-width: 600px) {
  footer {
    grid-template-columns: 1fr;
    text-align: center;

    >aside>h4{
      margin: auto;  
      
    }
  }

}
